<template>
	<div id=friendsCircle >
		<div class="top" @click="showPopup">
			<div class="fixed" :class='isBg?"bg":""' @click.stop='goTop'>
				<img src="~img/images/friendsCircle/left.png" alt="" class='left' v-show='!isBg' @click.stop='$router.go(-1)'>
				<img src="~img/images/friendsCircle/left2.png" alt="" class='left' v-show='isBg' @click.stop='$router.go(-1)'>
				<span class='pyq' v-show='isBg'>{{nickName || userName}}</span>
			</div>
			<div class="myInfo">
				<p>{{userName}}</p>
				<img :src="avator" alt="" @click.stop='goInfo'>
			</div>	
		</div>
		<div class="list" v-for='(val,index) in friendsList'  :key='index' @click='nowIndex=null'>
			<img :src="val.avator" class='avator' @click.stop='goUserInfo(val,index)' >
			<div class="content">
				<p class="name" @click.stop='goUserInfo(val,index)'>{{val.nickName || val.name || myName}}</p>
				<p class='say'>{{val.say}}</p>
				<p v-if='val.attached_image' class='attached_image' >
					<img :src="i" alt=""  v-for='(i,index) in val.attached_image' :class='val.attached_image.length>1?"styleImg":""' @click='showImgs(val.attached_image,index)'>
				</p>
				<span class='time'>{{val.create_time}}</span>
				<span class="more" @click.stop='showMore(index)'></span>
				<ul class='moreList' :class='index===nowIndex?"showMore":""'>
					<li>
						<img src="~img/images/friendsCircle/zan.png" alt="">
						赞
					</li>
					<li>
						<img src="~img/images/friendsCircle/comment.png" alt="">
						评论
					</li>
				</ul>
			</div>
		</div>
		<van-popup v-model="isShowPop">赞这个相册封面</van-popup>
		<van-image-preview
		  v-model="showImg"
		  :images="images"
		  :show-index='false'
		  :show-indicators='true'
		  :duration='0.2'
		  :start-position='nowImgindex'
		>
		</van-image-preview>
	</div>
</template>
<script>
	import list from '@/assets/json/friendsCircle.json'
	export default{
		name:'othersCircle',
		data(){
			return{
				isBg:false,
				isShowMore:false,
				friendsList:null,
				nowIndex:null,
				isShowPop:false,
				avator:null,
				userName:null,
				weChat:null,
				nickName:null,
				showImg:false,
				images:[],
				nowImgindex:0,
				myName:sessionStorage.getItem('myName')
			}
		},
		methods:{
			onRefresh() {
		      setTimeout(() => {
		        this.$toast('刷新成功');
		        this.isLoading = false;
		      }, 400);
		    },
		    showMore(index){	
		    	if(this.nowIndex===index){
		    		this.nowIndex=null;
		    	}
		    	else{
		    		this.nowIndex=index;
		    	}	
		    },
		    goUserInfo(val,index){
		    	if(!val.nickName || !val.name){
		    		this.goMyInfo();
		    		return;
		    	}
		    	sessionStorage.setItem('nowChatUser',val.name)
				sessionStorage.setItem('nowUserAvator',val.avator)
				if(val.nickName){
					sessionStorage.setItem('nowChatNickname',val.nickName)
				}
				else{
					sessionStorage.setItem('nowChatNickname','')
				}
				sessionStorage.setItem('nowChatNum',val.wechatNum)
				sessionStorage.setItem('nowUserSex',val.sex)
				sessionStorage.setItem('nowUserArea',val.area)
				this.$router.push('/userInfo')
		    },
		    goInfo(){
		    	sessionStorage.setItem('nowChatUser',this.userName)
				sessionStorage.setItem('nowUserAvator',this.avator)
				if(this.nickName){
					sessionStorage.setItem('nowChatNickname',this.nickName)
				}
				else{
					sessionStorage.setItem('nowChatNickname','')
				}
				sessionStorage.setItem('nowChatNum',this.weChat)
				sessionStorage.setItem('nowUserSex',this.sex)
				sessionStorage.setItem('nowUserArea',this.area)
				this.$router.push('/userInfo')
		    },
		    goMyInfo(){
		    	let myName=sessionStorage.getItem('myName');
		    	sessionStorage.setItem('nowChatUser',myName)
				sessionStorage.setItem('nowUserAvator','http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg')
				sessionStorage.setItem('nowChatNickname','')
				sessionStorage.setItem('nowChatNum','Maple_Story')
				sessionStorage.setItem('nowUserSex','男')
				sessionStorage.setItem('nowUserArea','中国 浙江')
				this.$router.push('/userInfo')
		    },
		    goTop(){
		    	document.documentElement.scrollTop=0;
		    	document.body.scrollTop=0;
		    	this.isLoading=true;
		    	this.nowIndex=null;
		    	this.onRefresh();
		    },
		    showPopup() {
		      this.isShowPop = true;
		    },
		    showImgs(val,nowImgindex){
		    	this.images=val;
		    	this.showImg=true;
		    	this.nowImgindex=nowImgindex;
		    }
		},
		mounted(){
			this.friendsList=list.data;

		},
		activated() {
			this.avator=sessionStorage.getItem('nowUserAvator');
			var name=sessionStorage.getItem('nowChatNickname');
			if(name){
				this.nickName=sessionStorage.getItem('nowChatNickname');
			}
			this.userName=sessionStorage.getItem('nowChatUser');
			this.weChat=sessionStorage.getItem('nowChatNum');
			this.sex=sessionStorage.getItem('nowUserSex');
			this.area=sessionStorage.getItem('nowUserArea');
			// let friendsTop=sessionStorage.getItem('friendsTop');
			// if(friendsTop){
			// 	document.documentElement.scrollTop=document.body.scrollTop=friendsTop;
			// }	
	      	window.onscroll=()=>{
	      		this.nowIndex=null;
	      		// var sTop=document.documentElement.scrollTop|| document.body.scrollTop;
	      		// sessionStorage.setItem('friendsTop',sTop);
	      		var winScrollTop=document.documentElement.scrollTop|| document.body.scrollTop;
				if(winScrollTop>240){
					this.isBg=true;								
				}
				else{
					this.isBg=false;
				}
	      	} 
	    },
	    deactivated () {
		    window.onscroll=null;
		    this.nowIndex=null;
		    this.nickName=null;	
		    this.isBg=false;	   
		},
		beforeRouteLeave (to, from, next) {
			this.nowIndex=null;
			if(this.isShowPop){
				this.isShowPop=false;
				let friendsTop=document.documentElement.scrollTop|| document.body.scrollTop;
				document.documentElement.scrollTop=document.body.scrollTop=friendsTop;
				this.$router.go(1);
		    }	
		    else if(this.showImg){
		    	this.showImg=false;
				this.$router.go(1);
		    }
		    else{
		    	next();
		    }	
		}
	}
</script>
<style scoped>
	#friendsCircle{
		padding-bottom: 1rem;
		background: #fff;
	}
	.top{
		height: 44vh;
		background:url(https://images.pexels.com/photos/247600/pexels-photo-247600.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) no-repeat center/cover;
		position: relative;
		margin-bottom: 1rem;
	}
	.fixed{
		position: fixed;
	    width: 100%;
	    display: flex;
	    align-items: center;
	    padding: .5rem 0;
	    transition: .3s;
	    z-index: 6;
	    top:0;
	    left:0;
	}
	.bg{
		background: #ededed;
	}
	.fixed img{
		width: .48rem;
		height: .48rem;
	}
	.fixed .left{
		position: absolute;
   		left: .3rem;
   		width: .42rem;
		height: .42rem;
	}
	.camera{
	    position: absolute;
	    right: .3rem;
	}
	.pyq{
		font-size: .43rem;
	    position: absolute;
	    left: 1rem;
	}
	.myInfo{
		display: flex;
	    align-items: center;
	    position: absolute;
	    bottom: -.3rem;
	    right: .3rem;
	}
	.myInfo>p{
		font-size: .38rem;
	    font-weight: bold;
	    color: #fff;
	    margin-right: .3rem;
	}
	.myInfo>img{
		width: 1.27rem;
	    height: 1.27rem;
	    border-radius: .12rem;
	}
	.list{
		display: flex;
		background: white;
	    padding: .2rem .5rem .2rem .2rem;
	    border-bottom:1px solid rgba(233,233,233,.6);
	}
	.list .avator{
		width: .87rem;
	    height: .87rem;
	    margin-right: .2rem;
	    border-radius: .1rem;
	}
	.list .content{
		position: relative;
		width: 85%;
	}
	.list .content .name{
		color: #626985;
   	    margin-bottom: .08rem;
   	    font-size: .38rem;
	}
	.list .content .say{
		font-size: .36rem;
		margin-bottom: .3rem;
	}
	.list .content .time{
		font-size: .3rem;
	    color: #888;
	    display: block;
	    margin: .22rem 0;
	}
	.list .content .more{
		width: .5rem;
		height: .4rem;
		right: 0;
	    bottom: .13rem;
	    border-radius: .1rem;
	    position: absolute;
	    padding: 0.02rem;
		background:#f8f8f8 url('~img/images/friendsCircle/more.png') no-repeat center/cover;
	}
	.moreList{
		background: #2f2f2f;
	    font-size: .33rem;
	    color: #fff;
	    width: 61%;
	    border-radius: .1rem;
	    display: flex;
	    padding:.2rem;
	    position: absolute;
	    right: .85rem;
	    bottom: -.1rem;
		opacity: 0;
		transition: .2s;
		transform: rotateY(90deg);
	}
	.moreList:after{
		content: "";
		display: inline-block;
		width: 0;
	    height: 0;
	    border-left: .2rem solid #2f2f2f;
	    border-right:  .2rem solid transparent;
	    border-bottom: .2rem solid transparent;
	    border-top: .2rem solid transparent;
		position: absolute;
		right: -.32rem;
		bottom:.25rem;
	}
	.moreList li{
		display: flex;
		width: 50%;
    	align-items: center;
    	justify-content: center;
	}
	.moreList li img{
		width: .5rem;
    	height: .5rem;
	}
	.showMore{
	    transform: rotateY(0);
	    opacity: 1;
	}
	.attached_image{
		display: flex;
		flex-wrap: wrap;
	}
	.attached_image img{
		max-width: 87%;
		height: 2.8rem;
		margin-bottom: .12rem;
	}
	.attached_image img.styleImg{
		width: 1.6rem;
	    height: 1.6rem;
	    margin: .06rem;
	}
	.van-popup--center{
		font-size: .39rem;
		padding: .32rem;
    	padding-left: .4rem;
    	width: 90vw;
		border-radius: .17rem;
		box-sizing: border-box;
	}
	.van-popup--center ul li{
		font-size: .38rem;
		padding: .25rem;
	}
	.van-popup--center ul li:first-child{
		border-bottom: 1px solid #eee;
		position: relative;
	}
	.van-popup--center ul li:first-child span{
		font-size: .3rem;
	    position: absolute;
	    right: .1rem;
	    top: .2rem;
	}
	.van-popup--center ul li:first-child span{
		font-size: .3rem;
	    position: absolute;
	    right: .1rem;
	    top: .2rem;
	}
	#friendsCircle >>> .van-image-preview__overlay  {
		background: rgba(0,0,0,1);
	}
	#friendsCircle >>> .van-swipe__indicators {
		bottom: .6rem;
	}
	#friendsCircle >>> .van-swipe__indicator:not(:last-child) {
	    margin-right: .18rem;
	}
</style>


